* {
    box-sizing: border-box;
}

html {
    height: 100%;
    margin: 0;
}

body {
    background: rgb(10, 31, 39);
    font-family: Arial, Helvetica, sans-serif;
    padding: 0;
    height: 95%;
    position: relative;
    top: 0;
    bottom: 0;
    margin: 0;
}

h1 {
    color: whitesmoke;
}

.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: 0 auto;
    padding: 2em;
    max-width: 50em;
}

.item {
    display: flex;
    border-radius: 10px;
    background: rgb(255, 245, 238);
    width: 100%;
    padding: 1em;
    margin: 1em 0;
}

.one-up {
    position: relative;
    background-color: lightgoldenrodyellow;
    align-self: center;
    margin-left: 1em;
}

.one-up::before {
    content: '';
    color: rgb(253, 191, 162);
    text-shadow: cyan 1px 1px;
    font-size: 20px;
    position: absolute;
    z-index: 2;
    top: 1em;
    left: 0em;
}

.one-up::after {
    content: '';
    color: cyan;
    text-shadow: rgb(253, 191, 162) 1px 1px;
    font-size: 20px;
    position: absolute;
    z-index: 2;
    top: 1em;
    left: 0em;
}

.one-up select {
    font-size: 1em;
    background-color: rgb(225, 239, 245);
    padding: 1em;
    border: none;
    min-width: 5em;
}

.one-up select option {
    border: none;
    background-color: rgb(225, 239, 245);
}